.site-header {
    position: fixed;
    width: 100%;
    background: $mainBlack;
    z-index: 10;
}

header {
    .container {
        display: flex !important;
        max-width: 1100px;
        padding: 0 15px;
        margin: auto;
    }
}

#masthead {
    a {
        color: #ffffff;
    }
}

a.custom-logo-link {
    max-width: 250px !important;
}

/*About page header */
.page-id-44 .article-header {
    background-image: url(https://cafejindo.ca/wp-content/uploads/2021/01/food-window-scaled.jpg);
}

/*Menu page header */
.post-type-archive-menu .article-header {
    background-image: url(https://cafejindo.ca/wp-content/uploads/2021/05/menu-servings.jpg);
    background-position: 50% 122%;
    background-size: cover;
}

/*Contact page header */
.page-id-13 .article-header {
    background-image: url(https://cafejindo.ca/wp-content/uploads/2021/04/store-counter.jpg);
    background-position: 50% 23%;
    background-size: cover;
}

/*Order online header */
.page-id-393 .article-header {
    background-image: url(https://cafejindo.ca/wp-content/uploads/2021/04/store-lightbulbs.jpg);
    background-position: 50% 73%;
